.products {
    background-color: var(--primary-bg);
    padding: 80px 0px 40px;
    @media screen and (min-width: 768px) {
        padding: 40px 0px 60px;
    }
    @media screen and (min-width: 1280px) {
        padding: 60px 0px;
    }

}

.products__label {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;

    text-align: center;
    text-transform: uppercase;
    color: var(--accent-text);

    @media screen and (min-width: 768px) {
        font-size: 26px;
        line-height: 1.15;
    }
    @media screen and (min-width: 1280px) {
        font-size: 30px;
        line-height: 1.13;
        letter-spacing: 0.04em;
    }
}

.products__title {
    margin-top: 15px;

    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.14;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--primary-accent);
    @media screen and (min-width: 768px) {
        margin-top: 18px;
        font-size: 48px;
    }
    @media screen and (min-width: 1280px) {
        margin-top: 16px;
        font-size: 58px;
        letter-spacing: 0.06em;
    }
}
.products-list {
    @media screen and (min-width: 768px) {
        display: flex;
        margin-left: -16px;
    }
    @media screen and (min-width: 1280px) {
        margin-left: -32px;
    }
}

.products-list__item {
    position: relative;

    margin-top: 140px;
    width: 100%;
    height: 489px;

    border-radius: 24px;
    @media screen and (min-width: 768px) {
        margin-top: 154px;
        margin-left: 16px;
        flex-basis: calc(100%/3 - 16px);
        height: 473px;
    }
    @media screen and (min-width: 1280px) {
        margin-top: 258px;
        margin-left: 32px;
        flex-basis: calc(100%/3 - 32px);
        height: 535px;
    }

    &:first-child {
        background: var(--hero-bg);
        &::before {
            position: absolute;
            left: 0;
            top: 0;
            transform: translateY(-50%);
            display: block;
            content: "";
            width: 100%;
            height: 280px;
            background-image: url(../images/products-img1-mob.png);
            @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
            background-image: url(../images/products-img1-mob-2x.png);
            }
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;

        @media screen and (min-width: 768px) {
                background-image: url(../images/products-img1-tab.png);
            @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image: url(../images/products-img1-tab-2x.png);
                }
                transform: translateY(-60%);
                height: 224px;
            }
        @media screen and (min-width: 1280px) {
                background-image: url(../images/products-img1.png);
            @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image: url(../images/products-img1-2x.png);
                }
                transform: translateY(-66%);
                height: 394px;
            }
        }
    }

    &:nth-child(2) {
        background: var(--card-bg-second);
        &::before {
            position: absolute;
            left: 0;
            top: 0;
            transform: translateY(-50%);
            display: block;
            content: "";
            width: 100%;
            height: 280px;
            background-image: url(../images/products-img2-mob.png);
            @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
                background-image: url(../images/products-img2-mob-2x.png);
            }
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;

        @media screen and (min-width: 768px) {
                background-image: url(../images/products-img2-tab.png);
            @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image: url(../images/products-img2-tab-2x.png);
                }
                transform: translateY(-60%);
                height: 224px;
            }

        @media screen and (min-width: 1280px) {
                background-image: url(../images/products-img2.png);
            @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image: url(../images/products-img2-2x.png);
                }
                transform: translateY(-66%);
                height: 394px;
            }
        }
    }

    &:nth-child(3) {
        background: var(--card-bg-third);

        &::before {
            position: absolute;
            left: 0;
            top: 0;
            transform: translateY(-50%);
            display: inline-block;
            content: "";
            width: 100%;
            height: 280px;
            background-image: url(../images/products-img3-mob.png);
            @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
                background-image: url(../images/products-img3-mob-2x.png);
            }
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
        @media screen and (min-width: 768px) {
                background-image: url(../images/products-img3-tab.png);
            @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image: url(../images/products-img3-tab-2x.png);
                }
                transform: translateY(-60%);
                height: 224px;
            }
        @media screen and (min-width: 1280px) {
                background-image: url(../images/products-img3.png);
            @media (min-device-pixel-ratio: 2),
                (min-resolution: 192dpi),
                (min-resolution: 2dppx) {
                    background-image: url(../images/products-img3-2x.png);
                }
                transform: translateY(-66%);
                height: 394px;
            }
        }
    }
}

.products-list__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 160px 40px 40px;
    height: 100%;
    @media screen and (min-width: 768px) {
        padding: 118px 31px 40px;
    }
    @media screen and (min-width: 1280px) {
        padding: 165px 76px 60px;
    }
}

.products-list__title {
    position: relative;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    text-align: center;
    color: var(--secondary-text);
    @media screen and (min-width: 1280px) {
        font-size: 30px;
        line-height: 1.13;
    }
}

.products-list__title::after {
  
    position: absolute;
    top: 145%;
    left: 43%;
    content: "...";
    display: inline-block;
    color: var(--secondary-text);
    font-size: 26px;
    @media screen and (min-width: 768px) {
    top: 160%;
    left: 43%;
    }
    @media screen and (min-width: 1280px) {
    top: 125%;
    left: 43%;
    }
}
